<template>
  <div class="InputComp">
    <div class="form">
      <div>
        <input class="email" type="email" placeholder="邮箱" v-model="email" />
      </div>
      <div>
        <input class="password" type="password" placeholder="密码" v-model="password" />
      </div>
      <div>
        <button-comp class="login" @click.native="login()">
          <template #content>登录</template>
        </button-comp>
      </div>
      <div class="register-box">
        <span @click="register" class="regiseter">新用户注册</span>
      </div>
    </div>
    <disable-comp title="没有后端了不能登录"></disable-comp>
  </div>
</template>

<script>
import InputComp from "components/common/input/InputComp.vue";
import ButtonComp from "components/common/input/ButtonComp.vue";

import { login } from "network/user";
import { login_check } from "common/utils";
import DisableComp from "./disableComp.vue";
export default {
  name: "LoginView",
  data() {
    return {
      email: "",
      password: "",
    };
  },
  methods: {
    login() {
      if (!login_check.email.test(this.email.trim())) {
        return this.$toast.show("邮箱格式不正确");
      }
      if (!login_check.password.test(this.password.trim())) {
        return this.$toast.show("密码格式不正确,密码只能是6~12位");
      }
      login(this.email, this.password).then((res) => {
        if (res.code === "ERROR") {
          return this.$toast.show(res.msg);
        }
        console.log(res);
        this.$emit("login-ok", this.email, res.token);
      });
    },
    register() {
      this.$router.push("/register");
    },
  },
  components: {
    InputComp,
    ButtonComp,
    DisableComp
},
};
</script>

<style scoped>
.InputComp {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  /* height: calc(100vh - 44px - 49px); */
  height: calc(100vh - 6.59670vh - 7.34632vh);
  position: relative;
}

.InputComp .form {
  width: 80%;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  justify-content: space-evenly;
  height: 40vh;
  min-height: calc(44 * 3px);
}

.email,
.password {
  width: 100%;
  height: 44px;
  outline-color: var(--color-tint);
  border-radius: 8px;
  padding: 5px 10px;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

.login {
  width: 100%;
  height: 44px;
  outline-color: var(--color-tint);
  border-radius: 10px;
  padding: 5px 10px;
  background-color: var(--color-tint);
  color: #fff;
}

.register {
  color: #ccc;
  font-size: 14px;
}
</style>