<template>
  <div class="profile">
    <nav-bar class="profile-nav">
      <template v-slot:center>
        <div>我的</div>
      </template>
    </nav-bar>
    <email-view
      v-if="getLoginStatus"
      @log-out="logOut"
      :email="getEmail"
    ></email-view>
    <login-view v-else @login-ok="loginOK"></login-view>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import NavBar from "components/common/navbar/NavBar.vue";
import LoginView from "./childComps/LoginView.vue";
import EmailView from "./childComps/EmailView.vue";

export default {
  name: "Profile",
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["getLoginStatus", "getEmail"]),
  },
  methods: {
    loginOK(email, token) {
      // console.log(token);
      this.$store
        .dispatch("loginOK", {
          email,
          token,
        })
        .then((res) => {
          this.$toast.show(res);
        });
    },
    logOut() {
      this.$store.dispatch("logOut").then((res) => {
        this.$toast.show(res);
      });
    },
  },
  components: {
    NavBar,
    LoginView,
    EmailView
},
};
</script>

<style scoped>
.profile-nav {
  background-color: var(--color-tint);
  color: #fff;
  font-weight: bold;
}
</style>